.section {
    width: 100%;
    height: 100vh;
    max-height: 100%;
    background-image: url(./vector.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  
  .banner {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .box {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 4%;
    padding-top: 150px;
  }
  
  .subBox {
    width: 100%;
    max-width: 600px;
    margin-top: -100px;
    list-style-type: none;
    text-align: center;
    border: 3px solid white;
    border-radius: 20px 10px;
    color: white;
    padding: 5em 1em;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
  }
  
  button {
    margin-top: 80px;
    cursor: pointer;
    background-color: rgb(42, 73, 140);
    border-radius: 15px 10px;
    border-bottom: 6px solid #01477a;
    box-shadow: 3px 5px 7px black;
  }
  
  button a {
    height: 55px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.5em;
    font-weight: bold;
    text-decoration: none;
    color: white;
    padding: 1px 10px;
  }
  
  button:hover {
    transition: 0.3s;
    background-color: rgb(42, 73, 140);
    border-bottom: 6px solid #023052;
    transform: scale(1.02);
  }
  
  button a span {
    text-shadow: 1px 1px 1px black;
  }
  
  .slogan {
    color: #ffffff;
    margin-top: 1em;
  }
  