.header {
  width: 100%;
  padding: 15px 2%;
  background-color: rgba(22, 23, 28, 0.4);
  position: fixed;
  top: 0;
  z-index: 999;
}

.header:hover {
  transition: 1s;
  background-color: rgba(15, 15, 15, 0.9);
  border-bottom: 1px solid #000;
}

.header.black {
  transition: 1s;
  background-color: rgba(15, 15, 15, 0.9);
  border-bottom: 1px solid #000;
}

.header > .center {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.header a {
  text-decoration: none;
  color: white;
  transition: 1s;
}

.header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .logo a img {
  height: 3.5em;
}


.header .menu {
  margin-top: 5px;
}

.header .desktop {
  display: block;
}

.header .desktop ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.header .desktop ul li {
  margin: 0 30px;
}

.header .desktop ul li a {
  font-size: 1.2em;
  font-weight: bold;
}

.header .desktop ul li a:hover {
  color: #0095ff;
}

.header .mobile {
  width: 100%;
  display: none;
}

.header .mobile ul {
  display: none;
  width: 100%;
  text-align: center;
  position: absolute;
  left: 0;
  top: 75px;
  background-color: rgba(15, 15, 15, 0.9);
}

.header .mobile ul.visible {
  display: block !important;
}

.header .mobile ul li a {
  display: block;
  font-weight: 200;
  padding: 20px 0;
  border-bottom: 1px solid #0095ff;
  font-size: 1.3em;
}

.header .mobile .icon svg {
  font-size: 2em;
  color: white;
  font-weight: 200;
  cursor: pointer;
}

@media screen and (max-width: 819px) {
  .header .desktop {
    display: none;
  }

  .header .mobile {
    display: block;
  }
}
