.tittle-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .tittle-container h2 {
    text-align: center;
    font-size: 2.7em;
    font-weight: 300;
    text-shadow: 1px 2px 2px #0095ff;
    padding: 0 10px;
  }
  
  .tittle-container .line {
    width: 100%;
    height: 2px;
    background-color: black;
    box-shadow: 0 2px 4px #0095ff;
  }
  