.image-container {
    width: 100%;
    height: 200px;
    background-image: url("bg.jpg");
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
  
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image-container > h2 {
    margin: auto;
    text-align: center;
    color: white;
    font-weight: 200;
    font-size: 2em;
    text-shadow: 1px 1px 2px black;
  }
  
  .contact-section {
    width: 100%;
    padding: 60px 0 15px 0;
  }
  
  .contact-section form {
    margin: 50px auto;
    width: 100%;
    max-width: 450px;
  }
  
  .contact-section form.dark {
    background-color: transparent;
  }
  
  .contact-section input[type="text"],
  .contact-section input[type="email"] {
    width: 100%;
    padding: 0 13px;
    height: 45px;
    line-height: 45px;
    margin: 15px 0;
    border-radius: 25px;
  
    background-color: rgb(5 26 40);
    box-shadow: 2px 7px 8px #00000050;
    color: white;
    border: 1px solid transparent;
  }
  
  .contact-section form.dark input[type="text"],
  .contact-section form.dark input[type="email"],
  .contact-section form.dark textarea {
    border: 1px solid #0095ff40;
    box-shadow: 2px 7px 8px #0095ff40;
  }
  
  .contact-section input[type="text"]:focus,
  .contact-section input[type="email"]:focus,
  .contact-section textarea:focus {
    border: 1px solid #0095ff;
    box-shadow: 2px 7px 8px #00000080;
  }
  
  .contact-section form.dark input[type="text"]:focus,
  .contact-section form.dark input[type="email"]:focus,
  .contact-section form.dark textarea:focus {
    border: 1px solid #0095ff;
    box-shadow: 0 4px 12px #0095ffe0;
  }
  
  .contact-section textarea {
    margin: 15px 0;
    width: 100%;
    height: 200px;
    padding: 13px;
    border-radius: 25px;
    resize: none;
    border: 1px solid transparent;
  
    background-color: rgb(5 26 40);
    box-shadow: 2px 7px 8px #00000050;
    color: white;
  }
  
  .contact-section input[type="submit"] {
    display: block;
    margin: 10px auto 0 auto;
  
    cursor: pointer;
    background-color: rgb(42, 73, 140);
    border-radius: 15px 10px;
    border-bottom: 6px solid #01477a;
    box-shadow: 2px 7px 8px #00000050;
  
    height: 55px;
    line-height: 55px;
    font-size: 1.4em;
    font-weight: 700;
    text-decoration: none;
    color: white;
    padding: 0 35px;
  }
  
  .contact-section form.dark input[type="submit"] {
    box-shadow: 2px 7px 8px #00000080;
  }
  
  .contact-section input[type="submit"]:hover {
    transition: 0.3s;
    background-color: rgb(42, 73, 140);
    border-bottom: 6px solid #023052;
    transform: scale(1.05);
  }
  