.footer {
    background-color: #000;
    padding: 30px 0;
    text-align: center;
}
.footer p {
    font-size: 20px;
    color: rgb(42, 73, 140);
    padding-bottom: 30px;
}

.footer .direitos {
    margin-top: 2em;
    font-size: 10px;
}

.footer  div a {
    text-decoration: none;
    font-size: 40px;
    color: rgb(42, 73, 140);
    padding-right: 15px;
}

.icon-facebook {
    color: #3b5998; /* cor padrão do Facebook */
    font-size: 34px;
    transition: color 0.2s ease-in-out;

    /* estilização ao passar o mouse */
    &:hover {
        color: #2d4373; /* cor escura do Facebook */
    }
}

.icon-instagram {
color: #e1306c; /* cor padrão do Instagram */
font-size: 34px;
transition: color 0.2s ease-in-out;

/* estilização ao passar o mouse */
&:hover {
    color: #bd2e5d; /* cor escura do Instagram */
}
}

  
.icon-linkedin {
color: #0e76a8; /* cor padrão do LinkedIn */
font-size: 34px;
transition: color 0.2s ease-in-out;

/* estilização ao passar o mouse */
&:hover {
    color: #0c5c87; /* cor escura do LinkedIn */
}
}
  

.icon-whatsapp {
    color: #25d366; /* cor padrão do WhatsApp */
    font-size: 34px;
    transition: color 0.2s ease-in-out;
  
    /* estilização ao passar o mouse */
    &:hover {
      color: #1ebea5; /* cor escura do WhatsApp */
    }
  }
  